<template>
    <div>
        <!-- Titulo ficha -->
        <div id="titulo">
            <p>{{translate.TR_FICHA_MEDICA}}</p>
            <p>{{translate.TR_NOME_HOSPITAL}}</p>
        </div>

        <!-- Botoes Ficha -->
        <div id="botoes">
            <div :class="{selected: selecionado == 0}">
                <p :style="[selecionado === 0 && styleSelect]">{{translate.TR_BUTTON_PESQUISAR}}</p>
            </div>

            <div 
                :class="{selected: selecionado == 1}"
            >
                <p :style="[selecionado === 1 && styleSelect]">{{translate.TR_BUTTON_REGISTRAR}}</p>
            </div>      
        </div>

        <!-- Rodapé ficha -->
        <div id="rodape">
            <p>{{consultas}} {{translate.TR_TEXT_CONSULTAS_1}}<br>{{translate.TR_TEXT_CONSULTAS_2}}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: "Home",
    props:{
        consultas:{
            type: Number,
            default: 0
        },
        translate:{
            
        },
        config:{

        }
    },
    data(){
        return({
            selecionado: 0,
            styleSelect:{
                backgroundColor: "rgba(23, 94, 248, 0.705)"
            }
        });
    },
    methods: {
        receberLua(event){
            if (event && event.data) {
                const lua = event.data;

                // console.log('Home');

                if(lua.upSelected ||lua.downSelected){
                    this.selecionado = this.selecionado === 0 ? 1 : 0;
                }

                else if(lua.enterSelected){
                    this.$emit('abrirScreen', this.selecionado === 0 ? 'Pesquisa' : 'Registrar');
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receberLua);

        this.styleSelect = {
            backgroundColor: this.config.CFG_COR_SELECT
        }
    },
    destroyed: function(){
        window.removeEventListener('message', this.receberLua);
    },
    watch:{
        config: function(){
            this.styleSelect = {
                backgroundColor: this.config.CFG_COR_SELECT
            }
        }
    }
}

</script>

<style>
    #botoes{
        width: 70%;
        margin: auto;    

        flex: 1;

        display: flex;
        flex-direction: column;

        justify-content: center;

        /* background-color: green; */
    }

    #botoes div p{
        width: 70%;
        padding: 1vh;
        background-color: #FCFCFC;
        margin: auto;    

        margin-bottom: 2vh;

        text-align: center;
        border-radius: 20px;

        font-size: 2.4vh;
        color: #666;
        border: 1px solid #aaa;

        transition: 0.5s all;
    }

    #botoes .selected > p{
        color: white;
        font-weight: 400;
        border: 1px solid #aaaaaa00;

        /* background-color: rgb(88, 167, 88); */
        /* background-color: rgb(221, 99, 110); */
        /* background-color: rgba(248, 23, 41, 0.705); */
        /* background-color: rgb(187, 3, 3); */
        /* background-color: rgba(23, 94, 248, 0.705); */

    }

    #rodape{
        padding: 2vh 0;
    }
    #rodape p{
        text-align: center;

        font-size: 2vh;
        color: #888;
    }
</style>