<template>
    <div class="resultado-body">
        <!-- Titulo resultado -->
        <div id="titulo-resultado">
            <p>{{translate.TR_RESULTADO_TITULO}}</p>
        </div>

        <!-- Loader -->
        <div class="loader"
            v-if="isLoading"
        ></div>

        <!-- Resultados -->
        <div id="resultados"
            v-else
        >
            <div
                v-for="(item, index) in resultado"
                v-bind:key="index"
                :class="{selected : selecionado === index}"
                :style="[selecionado === index && styleSelect]"
            >
                <p class="resultados-nome">{{item.name_user}}</p>
                <p class="resultados-consultas">{{translate.TR_COUNT_CONSULTA_TEXT_1}} {{item.consultations}} {{translate.TR_COUNT_CONSULTA_TEXT_2}}</p>
            </div>
            
        </div>

        <div class="no-one"
            v-if="resultado.length === 0 && !isLoading"
        ><p>{{translate.TR_NAO_ENCONTRADO}}</p></div>
    </div>
</template>

<script>
export default {
    name: "Resultado",
    props:{
        translate:{

        },
        config:{

        },
        NameResource: {
            type: String,
            default: "lg_teste"
        }
    },
    data(){
        return({
            isLoading: true,
            selecionado: 0,
            resultado: [{name_user: "Alex", consultations: 20}],      

            styleSelect:{
                borderBottom: "2px solid rgba(23, 94, 248, 0.705)"
            }  
        });
    },
    methods: {
        receberLua(event){
            if (event && event.data) {
                const lua = event.data;

                // console.log('Resultado');

                if(lua.loadedResult){
                    this.resultado = lua.result;
                    this.isLoading = false;
                }

                else if(lua.upSelected){
                    this.selecionado = this.selecionado === 0 ? this.resultado.length-1 : this.selecionado-1;

                    const el = this.$el.querySelector('.selected');
                    el.scrollIntoView(false);
                }
                else if(lua.downSelected){
                    this.selecionado = this.selecionado === this.resultado.length-1 ? 0 : this.selecionado+1;

                    const el = this.$el.querySelector('.selected');
                    el.scrollIntoView(true);
                }

                else if(lua.enterSelected){
                    const nome = this.resultado[this.selecionado].name_user;
                    const id_user = this.resultado[this.selecionado].id_user;
                    const consultas = this.resultado[this.selecionado].consultations;
                    this.$emit('abrirScreen', 'Paciente', { nome: nome, consultas: consultas });

                    fetch(`https://${this.NameResource}/getPacient`, {
                        method: 'POST',
                        body: JSON.stringify({
                            id_user: id_user
                        })
                    });
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receberLua);

        this.styleSelect = {
            borderBottom: "2px solid "+this.config.CFG_COR_SELECT
        }
    },
    destroyed: function(){
        window.removeEventListener('message', this.receberLua);
    },
    watch:{
        config: function(){
            this.styleSelect = {
                borderBottom: "2px solid "+this.config.CFG_COR_SELECT
            }
        }
    }
}
</script>

<style>
    .resultado-body{
        overflow: hidden;
    }
    #titulo-resultado{
        margin-top: 7vh;
    }

    #titulo-resultado p{
        text-align: center;
        font-size: 3vh;

        color: #888;
        font-weight: bold;

        width: 80%;
        margin: auto;

        padding-bottom: 2vh;
    }
    #resultados{
        width: 70%;
        margin: auto;
        margin-top: 2vh;
        
        overflow: auto;
        /* padding: 0vh 2vh; */
        /* flex: 1; */
    }

    #resultados::-webkit-scrollbar-track {
        background-color: #ffffff00;
    }
    #resultados::-webkit-scrollbar {
        width: 0.3vh;
        background: rgba(214, 57, 57, 0);
    }
    #resultados::-webkit-scrollbar-thumb {
        background: rgba(214, 57, 57, 0);
        border-radius: 20px;
    }

    #resultados div{
        border-bottom: 1px solid gray;

        margin-bottom: 2vh;
        padding: 1vh 0.5vh;
    }
    
    .resultados-nome{
        font-size: 2.4vh;

        color: #666;
        font-weight: bold;
    }
    .resultados-consultas{
        font-size: 1.5vh;

        color: #888;
    }
    #resultados .selected{
        border-bottom: 2px solid rgba(23, 94, 248, 0.705);
        /* background-color: rgb(126, 21, 21); */
        border-radius: 10px;

        margin-bottom: 2vh;
        padding: 1vh 0.5vh;
    }
    .no-one{
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .no-one p{
        border-bottom: 1px solid rgb(39, 39, 39);
        
        margin-bottom: 2vh;
        padding: 1vh 0.5vh;
    }

    .loader {
        border: 16px solid #f3f3f3;
        border-radius: 50%;
        border-top: 16px solid #3498db;
        width: 12vh;
        height: 12vh;
        -webkit-animation: spin 2s linear infinite; /* Safari */
        animation: spin 2s linear infinite;

        margin: 10vh auto;
    }

    /* Safari */
    @-webkit-keyframes spin {
        0% { -webkit-transform: rotate(0deg); }
        100% { -webkit-transform: rotate(360deg); }
    }

    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
</style>