<template>
    <div class="paciente-body">
        <!-- Titulo resultado -->
        <div id="titulo-paciente">
            <p class="paciente">{{nome}}</p>
            <p class="consultas">{{translate.TR_COUNT_CONSULTA_TEXT_1}} {{resultado.length}} {{translate.TR_COUNT_CONSULTA_TEXT_2}}</p>
        </div>

        <!-- Loader -->
        <div class="loader"
            v-if="isLoading"
        ></div>

        <div v-else>
        <!-- Consultas -->
        <div id="consulta-id">
            <div class="arrow-invertido"><img src="../assets/arrow.png" alt=""></div>
            <p>{{translate.TR_PACIENTE_N_CONSULTA}} {{indice+1}}</p>
            <div><img src="../assets/arrow.png" alt=""></div>
        </div>

        <div id="consulta-sangue">
            <p class="title-motivo">{{translate.TR_SANGUE_CONSULTA}}</p>
            <p class="text-motivo">{{translate.TR_SANGUE_TIPO}} {{resultado[indice].sangue}}</p>
        </div>

        <div id="consulta-motivo">
            <p class="title-motivo">{{translate.TR_MOTIVO_CONSULTA}}</p>
            <p class="text-motivo">{{resultado[indice].reason}}</p>
        </div>

        <div id="consulta-descricao">
            <p class="title-motivo">{{translate.TR_DESCRICAO_CONSULTA}}</p>
            <p class="text-motivo">{{resultado[indice].description}}</p>
        </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name: "Paciente",
    props:{
        nome: {type: String, default: 'No one'},
        consultas: {type: Number, default: 95},
        translate:{
            
        }
    },
    data(){
        return({
            isLoading: true,
            indice: 0,
            resultado: [
                {sangue: "A", reason: 'Dor de cabeça', description: "A paciente esteve com fortes dores de cabeça. Foi receitado que ela tome dorflex por 10 dias e depois deve retornar."},
                {sangue: "A", reason: 'Dor nas costas', description: "A paciente esteve com fortes dores de cabeça. Foi receitado que ela tome dorflex por 10 dias e depois deve retornar."},
            ]
        });
    },
    methods: {
        receberLua(event){
            if (event && event.data) {
                const lua = event.data;

                // console.log('Paciente');

                if(lua.loadedPacient){
                    this.resultado = lua.result;
                    this.isLoading = false;
                }
                else if(lua.leftSelected){
                    this.indice = this.indice === 0 ? this.resultado.length-1 : this.indice-1;
                }
                else if(lua.rightSelected){
                    this.indice = this.indice === this.resultado.length-1 ? 0 : this.indice+1;
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receberLua);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receberLua);
    }
}
</script>

<style>
    .paciente-body{
        overflow: hidden;
    }
    #titulo-paciente{
        margin-top: 7vh;
    }

    #titulo-paciente p{
        text-align: center;
        width: 80%;
        margin: auto;
    }
    #titulo-paciente .paciente{
        font-size: 3vh;

        color: #888;
        font-weight: bold;
    }

    #titulo-paciente .consultas{
        font-size: 1.6vh;

        color: #888;

        padding-bottom: 2vh;

        border-bottom: 2px solid rgb(122, 122, 122);
    }
    
    #consulta-id{
        width: 70%;
        margin: auto;
        margin-top: 4vh;
        
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    #consulta-id p{
        font-size: 2.8vh;
        font-weight: bold;
        color: #888;
    }

    #consulta-id div{
        width: 2vh;
    }
    #consulta-id div img{
        width: 100%;
    }
    .arrow-invertido{
        transform: rotateY(180deg);
    }

    #consulta-sangue{
        width: 80%;
        margin: auto;

        margin-top: 8vh;
    }

    #consulta-motivo{
        width: 80%;
        margin: auto;

        margin-top: 2vh;
    }

    #consulta-descricao{
        flex:2;

        width: 80%;
        margin: auto;

        margin-top: 2vh;

        word-wrap: break-word;
    }

    .title-motivo{
        font-size: 2.2vh;
        color: #444;
        font-weight: 500;
    }

    .text-motivo{
        font-size: 2vh;

        color: #888;

        /* white-space: nowrap; */
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 75ch;
    }
</style>