<template>
  <div id="corpo" :class="{esconder : !aberto}" :style="styleObject">
    <!-- logo ficha -->
    <div id="logo">
      <img :src="config.CFG_LOGO_IMG" alt="">
    </div>

    <!-- Pagina principal -->
    <Home class="page" v-if="page === 'Home'" @abrirScreen="abrirScreen" :consultas="consultas" :translate="translate" :config="config" :NameResource="NameResource"/>
    <!-- Pagina de pesquisa -->
    <Pesquisa class="page" v-if="page === 'Pesquisa'" @abrirScreen="abrirScreen" :translate="translate" :config="config" :NameResource="NameResource" />
    <!-- Resultado da pesquisa -->
    <Resultado class="page" v-if="page === 'Resultado'" @abrirScreen="abrirScreen" :translate="translate" :config="config" :NameResource="NameResource" />
    <!-- Dados do paciente -->
    <Paciente class="page" v-if="page === 'Paciente'" @abrirScreen="abrirScreen" :nome="nomePaciente" :consultas="consultasPaciente" :translate="translate" :NameResource="NameResource" />
    <!-- Novo Registro -->
    <Registrar class="page" v-if="page === 'Registrar'" @abrirScreen="abrirScreen" :translate="translate" :config="config" :NameResource="NameResource" />

  </div>
</template>

<script>

import Home from './screens/Home';
import Pesquisa from './screens/Pesquisa';
import Resultado from './screens/Resultado';
import Paciente from './screens/Paciente';
import Registrar from './screens/Registrar';

export default {
  name: "App",
  components: {
    Home,
    Pesquisa,
    Resultado,
    Paciente,
    Registrar
  },
  data(){ 
    return({
      page: "Home",
      aberto: false,

      nomePaciente: "No One",
      consultasPaciente: 0,
      consultas: 0,

      styleObject: {
        transform: "translateY(50%) scale(1.0)"
      },

      translate: {
        TR_FICHA_MEDICA           : "Ficha Médica",
        TR_NOME_HOSPITAL          : "Hospital de GTA V",
        TR_BUTTON_PESQUISAR       : "Pesquisar",
        TR_BUTTON_REGISTRAR       : "Registrar",
        TR_TEXT_CONSULTAS_1       : "consultas já foram",
        TR_TEXT_CONSULTAS_2       : "realizadas conosco!",
        TR_PESQUISA_TITULO        : "Pesquisar por",
        TR_PESQUISA_ID            : "ID",
        TR_PESQUISA_NOME          : "Nome",
        TR_PESQUISA_ALL           : "Ver todos os pacientes",
        TR_RESULTADO_TITULO       : "Pacientes Encontrados",
        TR_COUNT_CONSULTA_TEXT_1  : "Consultou",
        TR_COUNT_CONSULTA_TEXT_2  : "vezes",
        TR_NAO_ENCONTRADO         : "Nenhum paciente encontrado",
        TR_PACIENTE_N_CONSULTA    : "Consulta Nº",
        TR_SANGUE_CONSULTA        : "Tipo Sanguíneo:",
        TR_SANGUE_TIPO            : "Sangue tipo",
        TR_MOTIVO_CONSULTA        : "Motivo da consulta:",
        TR_DESCRICAO_CONSULTA     : "Descrição Médica:",
        TR_REGISTRO_TITULO        : "Novo Registro",
        TR_MAX_CARACTERES         : "Max 200 caracteres"
      },

      config:{
        CFG_POSITION_X: "90%",
        CFG_POSITION_Y: "50%",
        CFG_SIZE: "1.0",
        CFG_COR_SELECT: "rgba(23, 94, 248, 0.705)",
        CFG_LOGO_IMG: "./img/logo.png"
      },

      NameResource: "lg_teste"
    });
  },
  methods: {
    receberLua(event){
      if (event && event.data) {
        const lua = event.data;

        // console.log('App');

        if(lua.config){
          this.translate = lua.translate;
          this.config = lua.config;
          this.NameResource = lua.NameResource;

          const CFG_POSITION_X          = this.config.CFG_POSITION_X;
          const CFG_POSITION_Y          = this.config.CFG_POSITION_Y;
          const CFG_SIZE                = this.config.CFG_SIZE;

          this.styleObject = {
            left: CFG_POSITION_X,
            top: CFG_POSITION_Y,
            transform: "translate(-"+CFG_POSITION_X+", -"+CFG_POSITION_Y+") scale("+CFG_SIZE+")"
          }

        }
        else if(lua.open != null){
          this.aberto = lua.open;
        }
        else if(lua.goBack){
          if(this.page === "Home"){
            fetch(`https://${this.NameResource}/close`, {
              method: 'POST',
              body: JSON.stringify({})
            });
          }
          else{
            this.abrirScreen('Home');
          }
        }

        if(lua.countConsultations){
          this.consultas = lua.countConsultations;
        }
      }
    },

    abrirScreen(name, props = null){
      if(name == "Paciente"){
        this.nomePaciente = props.nome;
        this.consultasPaciente = props.consultas;
      }

      this.page = name;
    }
  },
  created: function(){
    window.addEventListener('message', this.receberLua);    
  },
  destroyed: function(){
    window.removeEventListener('message', this.receberLua);
  }
}

</script>

<style>
  *{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

    font-family: 'Roboto', sans-serif;
  }
  .esconder{
    display: none !important;
  }
  #corpo{
    width: 45vh;
    height: 75vh;

    background-color: #FCFCFC;
    box-shadow: 1px 1px 6px 1px #888;
    border-radius: 20px;

    position: absolute;
    left: 5%;
    bottom: 50%;
    
    transform: translateY(50%) scale(1.0);
    

    /* overflow: hidden; */

    display: flex;
    flex-direction: column;

    opacity: 0.95;
  }

  #logo{
    width: 10vh;
    height: 10vh;
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translate(-50%, -50%);

    border: 1px solid #CCC;
    box-shadow: 1px 1px 5px 1px #AAA;
    border-radius: 50%;
    background-color: white;
    padding: 1vh;

    text-align: center;
  }

  #logo img{
    width:100%;
  }

  #titulo{
    margin-top: 7vh;
  }

  #titulo p{
    text-align: center;
    font-size: 3vh;

    color: #888;
    font-weight: bold;

    width: 80%;
    margin: auto;
    
  }
  #titulo p:last-child{
    border-bottom: 1px solid black;
    padding-bottom: 2vh;
  }

  .page{
    flex: 1;
    display: flex;
    flex-direction: column;
  }
</style>