<template>
    <div class="corpo-registro">
        <!-- Titulo ficha -->
        <div id="titulo">
            <p>{{translate.TR_REGISTRO_TITULO}}</p>
        </div>

        <!-- Registro Ficha -->
        <div id="form-registro">
            <div :class="{selected : selecionado === 0}" :style="[selecionado === 0 && styleSelect]"><p>{{translate.TR_PESQUISA_ID}}</p><p class="valueForm">{{id_user}}</p></div>
            <div :class="{selected : selecionado === 1}" :style="[selecionado === 1 && styleSelect]"><p>{{translate.TR_PESQUISA_NOME}}</p><p class="valueForm">{{name_user}}</p></div>      
            <div :class="{selected : selecionado === 2}" :style="[selecionado === 2 && styleSelect]"><p>{{translate.TR_SANGUE_CONSULTA}}</p><p class="valueForm">{{sangue}}</p></div>      
            <div :class="{selected : selecionado === 3}" :style="[selecionado === 3 && styleSelect]"><p>{{translate.TR_MOTIVO_CONSULTA}}</p><p class="valueForm">{{reason}}</p></div>      
            <div class="registro-descricao" :class="{selected : selecionado === 4}" :style="[selecionado === 4 && styleSelect]"><p>{{translate.TR_DESCRICAO_CONSULTA}}</p><p class="valueForm">{{description}}</p></div>
            <div class="info-descricao"><p>{{translate.TR_MAX_CARACTERES}}</p></div>      
            <div class="botao" :class="{selected : selecionado === 5}" :style="[selecionado === 5 && styleButtonSelect]"><p>{{translate.TR_BUTTON_REGISTRAR}}</p></div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Registro",
    props:{
        translate:{

        },
        config:{

        },
        NameResource: {
            type: String,
            default: "lg_teste"
        }
    },
    data(){
        return({
            selecionado: 0,

            id_user                    : "",
            name_user                  : "",
            sangue                     : "",
            reason                     : "",
            description                : "",

            styleSelect:{
                borderBottom: "2px solid rgba(23, 94, 248, 0.705)"
            },
            styleButtonSelect:{
                backgroundColor: "rgba(23, 94, 248, 0.705)"
            }
        });
    },
    methods: {
        receberLua(event){
            if (event && event.data) {
                const lua = event.data;

                // console.log('Registrar');

                if(lua.upSelected){
                    this.selecionado = this.selecionado === 0 ? 5 : this.selecionado-1;
                }
                else if(lua.downSelected){
                    this.selecionado = this.selecionado === 5 ? 0 : this.selecionado+1;
                }
                else if(lua.downSelected){
                    this.selecionado = this.selecionado === 5 ? 0 : this.selecionado+1;
                }
                else if(lua.enterSelected){
                    if(this.selecionado === 0){
                        fetch(`https://${this.NameResource}/getRegisterId`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        }); 
                    }
                    else if(this.selecionado === 1){
                        fetch(`https://${this.NameResource}/getRegisterName`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        });
                    }
                    else if(this.selecionado === 2){
                        fetch(`https://${this.NameResource}/getRegisterSangue`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        });
                    }
                    else if(this.selecionado === 3){
                        fetch(`https://${this.NameResource}/getRegisterMotivo`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        });
                    }
                    else if(this.selecionado === 4){
                        fetch(`https://${this.NameResource}/getRegisterDescripton`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        });
                    }
                    else if(this.selecionado === 5){
                        if(this.id_user.length > 0 && this.name_user.length > 0 && this.sangue.length > 0 && this.reason.length > 0 && this.description.length > 0){
                            fetch(`https://${this.NameResource}/register`, {
                                method: 'POST',
                                body: JSON.stringify({
                                    id_user: this.id_user,
                                    name_user: this.name_user,
                                    sangue: this.sangue,
                                    reason: this.reason,
                                    description: this.description
                                })
                            });

                            this.$emit('abrirScreen', 'Home');
                        }
                    }
                }

                else if(lua.registerId){
                    this.id_user = lua.registerId;
                }

                else if(lua.registerName){
                    this.name_user = lua.registerName;
                }

                 else if(lua.registerSangue){
                    this.sangue = lua.registerSangue;
                }

                else if(lua.registerMotivo){
                    this.reason = lua.registerMotivo;
                }

                else if(lua.registerDescription){
                    this.description = lua.registerDescription;
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receberLua);

        this.styleSelect = {
            borderBottom: "2px solid "+this.config.CFG_COR_SELECT
        }
        this.styleButtonSelect = {
            backgroundColor: this.config.CFG_COR_SELECT
        }
    },
    destroyed: function(){
        window.removeEventListener('message', this.receberLua);
    },
    watch:{
        config: function(){
            this.styleSelect = {
                borderBottom: "2px solid "+this.config.CFG_COR_SELECT
            }
            this.styleButtonSelect = {
                backgroundColor: this.config.CFG_COR_SELECT
            }
        }
    }
}
</script>

<style>
    .corpo-registro{
        overflow: hidden;
    }
    #form-registro{
        width: 70%;
        margin: auto;

        flex: 1;
        display: flex;
        flex-direction: column;
        /* justify-content: center; */

        margin-top: 2vh;
        
    }

    #form-registro div{
        border-bottom: 2px solid gray;
        margin-bottom: 2.0vh;
    }
    /* #form-registro div.selected{
        border-bottom: 2px solid rgba(23, 94, 248, 0.705);
    } */

    #form-registro div p{
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 75ch;
    }

    #form-registro .registro-descricao{
        flex:1;
        margin-bottom: 0;

        max-height: 12vh;
        overflow: hidden;
    }

    #form-registro .registro-descricao p{
        flex:1;
        margin-bottom: 0;

        white-space: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 75ch;
    }

    #form-registro .info-descricao{
        border-bottom: none;
        margin-bottom: 0;
        min-height: 2vh;
    }
    #form-registro .info-descricao p{
        font-size: 1.4vh;
        text-align: center;
    }

    #form-registro .botao{
        border-bottom: none;

        width: 70%;
        padding: 1vh;
        /* background-color: rgb(88, 167, 88); */
        
        margin: auto;    

        margin-bottom: 2vh;

        text-align: center;
        border-radius: 20px;
        border: 1px solid #aaa;
        /* margin-bottom: 0; */
    }

    #form-registro .botao p{
        font-size: 2.0vh;
        text-align: center;
        color: #666;
    }

    #form-registro .botao.selected{
        /* background-color: rgb(88, 167, 88); */
        /* background-color: rgb(221, 99, 110); */
        /* background-color: rgba(248, 23, 41, 0.705); */
        /* background-color: rgb(187, 3, 3); */
        /* background-color: rgba(23, 94, 248, 0.705); */

        border-bottom: none !important;
    }

    #form-registro .botao.selected p{
        color: white;
    }

    #form-registro div p{
        font-size: 2.2vh;
        padding: 0.5vh;

        color: #888;
        font-weight: bold;
    }

    #form-registro .valueForm{
        font-size: 1.6vh;
    }

</style>