<template>
    <div>
        <!-- Titulo ficha -->
        <div id="titulo">
            <p>{{translate.TR_PESQUISA_TITULO}}</p>
        </div>

        <!-- Opcoes Pesquisa -->
        <div id="opcoes-pesquisa">
            <div :class="{selected : selecionado === 0}" :style="[selecionado === 0 && styleSelect]"><p>{{translate.TR_PESQUISA_ID}}</p></div>
            <div :class="{selected : selecionado === 1}" :style="[selecionado === 1 && styleSelect]"><p>{{translate.TR_PESQUISA_NOME}}</p></div>      
            <div :class="{selected : selecionado === 2}" :style="[selecionado === 2 && styleSelect]"><p>{{translate.TR_PESQUISA_ALL}}</p></div>   
        </div>
    </div>
</template>

<script>
export default {
    name: "Pesquisa",
    props:{
        translate:{

        },
        config:{

        },
        NameResource: {
            type: String,
            default: "lg_teste"
        }
    },
    data(){
        return({
            selecionado: 0,    
            styleSelect:{
                borderBottom: "2px solid rgba(23, 94, 248, 0.705)"
            }        
        })
    },
    methods: {
        receberLua(event){
            if (event && event.data) {
                const lua = event.data;

                // console.log('Pesquisa');

                if(lua.upSelected){
                    this.selecionado = this.selecionado === 0 ? 2 : this.selecionado-1;
                }
                else if(lua.downSelected){
                    this.selecionado = this.selecionado === 2 ? 0 : this.selecionado+1;
                }

                else if(lua.enterSelected){
                    if(this.selecionado === 0){
                        fetch(`https://${this.NameResource}/searchId`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        }); 
                    }
                    else if(this.selecionado === 1){
                        fetch(`https://${this.NameResource}/searchName`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        });  
                    }
                    else if(this.selecionado === 2){
                        fetch(`https://${this.NameResource}/searchAll`, {
                            method: 'POST',
                            body: JSON.stringify({})
                        });  
                    }

                    this.$emit('abrirScreen', 'Resultado');
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receberLua);

        this.styleSelect = {
            borderBottom: "2px solid "+this.config.CFG_COR_SELECT
        }
    },
    destroyed: function(){
        window.removeEventListener('message', this.receberLua);
    },
    watch:{
        config: function(){
            this.styleSelect = {
                borderBottom: "2px solid "+this.config.CFG_COR_SELECT
            }
        }
    }
}
</script>

<style>
    #opcoes-pesquisa{
        width: 70%;
        margin: auto;

        flex: 1;
        display: flex;
        flex-direction: column;
        /* justify-content: center; */

        margin-top: 10vh;
    }

    #opcoes-pesquisa div{
        border-bottom: 2px solid gray;
        margin-bottom: 3.5vh;
    }

    /* #opcoes-pesquisa .selected{
        border-bottom: 2px solid rgba(23, 94, 248, 0.705);
    } */

    #opcoes-pesquisa div p{
        font-size: 2.4vh;
        padding: 0.5vh;

        color: #888;
        font-weight: bold;
    }
</style>